.conditions {
    padding: 70px 0 90px 0;
}



.conditions-block {
    margin-top: 20px;
}

.conditions-list-title {
    color: #1CA8C3;
    font-weight: 400;
    font-size: 22px;
    line-height: 24px;
    margin: 0;
}


.conditions-list {
    margin: 0;
    padding-left: 50px;
    margin-top: 15px;
}

.conditions-list-item {
    font-weight: 300;
    font-size: 17px;
    line-height: 28px;
    color: #222222;
    list-style-type: none;
    position: relative;
    &::before {
        content: '';
        display: block;
        position: absolute;
        background-image: url('../icons/conditions/check_blue.svg');
        // background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        width: 21px;
        height: 20px;
        left: -30px;
        top: 3px;
    }
}

.conditions-text {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #1CA8C3;
    margin-top: 25px;
}

.conditions-red {
    color:#FF5656;
}

.conditions-red-item {
    &::before {
        background-image: url('../icons/conditions/check_red.svg');
     
    }
}

.mt--conditions {
    margin-top: 60px;
}