.world {
    background-color: #080816;
    padding: 50px 0;
    height: 720px;
    background-image: url('../img/world/world_bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.world-link {
    font-weight: 400;
    font-size: 15px;
    color: #1FBAD6;
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    text-align: center;
    margin-top: 450px;
    transition-duration: .3s;
    &:hover {
        color: white;
    }
    
}