        justify-content: center;
    }

    .footer-links-top {
        justify-content: center;
        a {
            font-size: 15px;
            margin-right: 5px;
        }
    }

    .footer-links-center {
        justify-content: center;
        a {
            margin-right: 5px;
        }
    }
    .download-icons {
        margin-top: 10px;
    }
    .download-icons {
        a:last-child {
            margin-right: 0;
        }
    }
}






@media(max-width: 769px) {
    header {
        height: 40px;
    }
    .nav__list {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        background-color: rgba(0,0,0, .8);
        z-index: 1;
        transition-duration: 0.6s;
        &_active {
            left: 0;
        }
    }
    .nav__item {
        border: none;
        padding: 0;
        width: 100%;
        text-align: center;
        margin-top: 15px;
        &:first-child {
            margin-top: 0;
        }
    }
    .nav__link {
        font-size: 18px;
        line-height: 20px;
        padding: 10px;
    }
    .hamburger {
        display: block;
        position: absolute;
        width: 26px;
        top: 50%;
        transform: translateY(-50%);
        right: 10px;
        span {
            display: block;
            margin-bottom: 2px;
            width: 100%;
            height: 2px;
            background-color: white;
            transition-duration: .5s;
            &:nth-child(3) {
                margin-bottom: 0px;
            }
        }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: rotate(-45deg);
                }
                &:nth-child(2) {
                    display: none;
                }
                &:nth-child(3) {
                    transform: rotate(45deg);
                }
            }
        }
    }
    .mobile-hidden {
        display: none;
    }
    .header__logo-text {
        display: none;
    }
    .mobile-space-between {
        justify-content: space-between;
    }
    .header__mobile-phone {
        display: block;
        text-align: center;
        font-size: 12px;
        &:hover {
            color: white;
        }
    }
    .header__btn {
        // width: 110px;
        width: 100%;
        height: 30px;
        font-size: 10px;
        margin-top: 5px;
    }
    .header__logo {
        a {
            margin-top: 10px;
            display: block;
        }
    }
    .promo {
        padding: 150px 0 90px 0;
    }
    .promo-title {
        font-size: 37px;
        line-height: 37px;
    }
    .promo-title-2 {
        font-size: 23px;
        line-height: 35px;
    }
    .repet-title {
        font-size: 27px;
    }
    .work-circle {
        width: 120px;
        height: 120px;
        margin-bottom: 25px;
    }
    .work-text {
        max-width: 100%;
        margin-left: 0;
        padding: 0 20px;
        h4 {
            font-size: 20px;
            line-height: 26px;
        }
        p {
            font-size: 18px;
            line-height: 25px;
        }
    }
    .mobile-block {
        width: 100%;
        height: 280px;
    }
    .mobile-title {
        font-size: 24px;
        line-height: 31px;
        padding-top: 80px;
    }
    .mobile-plus {
        width: 35px;
        height: 35px;
    }
}







@media(max-width: 576px) {
    header {
        height: 30px;
    }
    .header__btn {
        width: 100%;
        font-size: 12px;
    }
    .nav__list {
        width: 80%;
    }
    .hamburger {
        width: 20px;
    }
    .sub-header {
        padding: 12px 0;
    }
    .promo {
        padding: 125px 0 60px 0;
        min-height: 400px;
    }
    .promo-title {
        font-size: 36px;
    }
    .promo-title-2 {
        line-height: 28px;
        margin-top: 20px;
        font-size: 22px;
    }
    .promo-text {
        display: none;
    }
    .promo-text-2 {
        display: none;
    }
    .promo-btn {
        width: 210px;
        height: 50px;
        font-size: 16px;
        margin-top: 65px;
    }
    .repet-title {
        font-size: 16px;
        line-height: 25px;
        margin-top: 16px;
        margin-bottom: 20px;
    }
    .mobile-block {
        width: 95%;
        height: 260px;
    }
    .mobile-title {
        font-size: 20px;
        line-height: 31px;
        padding-top: 80px;
    }
    .mobile-block--margin-1 {
        margin-left: auto;
    }
    .mobile-block--margin-4 {
        margin-left: auto;
    }
    .mobile-block--margin-3 {
        margin-right: auto;
    }
    .mobile-block--margin-6 {
        margin-right: auto;
    }


    .car-bg {
        padding: 60px 0 420px 0;
    }

    .car-bg-2 {
        display: none;
    }
    .car-mobile {
        max-width: 400px;
        max-height: 250px;
    }
    .car-text-abs {
        display: block;
    }


    .conditions {
        padding: 30px 0 30px 0;
    }
    .conditions-list-title {
        font-size: 16px;
        line-height: 24px;  
    }
    .conditions-list {
        padding-left: 30px;
    }
    .conditions-list-item {
        font-size: 14px;
        line-height: 20px;
        &::before {
            width: 18px;
            height: 17px;
            left: -25px;
        }
    }
    .conditions-text {
        font-size: 14px;
        line-height: 16px;
    }
    .mt-mobile {
        margin-top: 0;
    }
    .mt--conditions {
        margin-top: 20px;
    }
    .world {
        height: 360px;
        background-size: cover;
    }
    .world-link {
        margin-top: 150px;
        font-size: 14px;
    }
    .repet-subtitle {
        font-size: 15px;
    }
}