.mobile-app {
    background: url('../img/mobile/mobile_bg.png') center/cover no-repeat;
    padding: 50px 0 80px 0;
}

.repet-subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #222222;
    text-align: center;
    margin: 0;
}
.mobile-block {
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-top: 40px;
    position: relative;
}
.mobile-title {
    font-weight: 400;
    font-size: 24px;
    line-height: 31px;
    color: white;
    text-transform: uppercase;
    text-align: center;
    padding-top: 80px;
}
.mobile-plus {
    width: 26px;
    height: 26px;
    background-color: black;
    border-radius: 100%;
    position: absolute;
    bottom: 35px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    transition-duration: .3s;
    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 14px;
        height: 2px;
        background-color: #1EACC7;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        &:hover {
            background-color: white;
        }
    }
    &::after {
        content: '';
        display: block;
        position: absolute;
        width: 2px;
        height: 14px;
        background-color: #1EACC7;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
    &:hover {
        background-color: #1EACC7;
        &::before, &::after {
            background-color: white;
            transition-duration: .3s;
        }
    }
}
.mobile-img-1 {
    background: url('../icons/mobile/card-1.png') center/cover no-repeat;
}
.mobile-img-2 {
    background: url('../icons/mobile/card-2.png') center/cover no-repeat;
}
.mobile-img-3 {
    background: url('../icons/mobile/card-3.png') center/cover no-repeat;
}
.mobile-img-4 {
    background: url('../icons/mobile/card-4.png') center/cover no-repeat;
}
.mobile-img-5 {
    background: url('../icons/mobile/card-5.png') center/cover no-repeat;
}
.mobile-img-6 {
    background: url('../icons/mobile/card-6.png') center/cover no-repeat;
}

.mobile-block--margin-1 {
    margin-left: 0;
}
.mobile-block--margin-4 {
    margin-left: 0;
}
.mobile-block--margin-3 {
    margin-right: 0;
}
.mobile-block--margin-6 {
    margin-right: 0;
}