.color-white {
    color: white;
}
.color-bg-white {
    background-color: white;
    color: black;
}

.car-bg {
    background-color: black;
    padding: 60px 0 340px 0;
    position: relative;
}

.car-img {
    background-image: url('../img/car/car.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 272px;
    left: 50%;
    transform: translateX(-50%);
}

.car-bg-2 {
    padding: 250px 0 110px 0;
    background: url('../img/car/car-bg.png') center/cover no-repeat;
}

.car-text {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #222;
    text-align: center;
}
.car-text-abs {
    display: none;
    position: absolute;
    width: 100%;
    padding: 0 30px;
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: white;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    bottom: 30px;
}