.promo {
    background: url('../img/header/uber_bg.png') center/cover no-repeat;
    padding: 180px 0 150px 0;
}

.promo-title {
    font-weight: 700;
    font-size: 60px;
    line-height: 60px;
    color: white;
    text-align: center;
    margin: 0;
}

.promo-title-2 {
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    color: white;
    text-align: center;
    margin: 0;
    margin-top: 10px;
}

.promo-text {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: white;
    text-align: center;
    margin: 0;
    margin-top: 20px;
}

.promo-text-2 {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: white;
    text-align: center;
    margin: 0;
    margin-top: 20px;
}

.promo-btn {
    display: block;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-weight: 400;
    font-size: 18px;
    color: white;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    &:hover {
        background: linear-gradient(0deg, #1EACC7 0%, #1EACC7 100%);
    }
}