header {
    background-color: #212121;
    height: 50px;
    position: sticky;
    top: 0;
    z-index: 1;
    nav {
        padding: 0 70px;
    }
}

.nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 0;
    height: 50px;
}

.nav__item {
    list-style-type: none;
    padding: 8px 0;
    padding-right: 45px;
    border-right: 2px dotted black;
    &:last-child {
        padding-right: 0;
        border-right: none;
    }
}

.nav__link {
    display: block;
    text-decoration: none;
    font-weight: 300;
    font-size: 15px;
    line-height: 15px;
    color: white;
    transition-duration: 0.3s;
    &:hover {
        color:#1EACC7;
    }
}

.hamburger {
    display: none;
}

.sub-header {
    background-color: rgba( 0, 0, 0, .6);
    padding: 19px 0;
    position: absolute;
    width: 100%;
}

.header__logo-text {
    font-weight: 100;
    font-size: 13px;
    line-height: 13px;
    margin-top: 6px;
    color: white;
}

.header__call {
    margin-top: 3px;
}

.header__call-text {
    font-weight: 300;
    font-size: 12px;
    line-height: 12px;
    color: white;
}

.header__call-phone {
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: white;
    text-decoration: none;
    margin-top: 8px;
    display: block;
    transition-duration: 0.3s;
    &:hover {
        color:#1EACC7;
    }
}

.header__mobile-phone {
    display: none;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: white;
    text-decoration: none;
    text-align: center;
}

.header__btn {
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    width: 172px;
    height: 46px;
    border: none;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    color: white;
    &:hover {
        background: linear-gradient(0deg, #1EACC7 0%, #1EACC7 100%);
    }
}