footer {
    padding: 10px 0 30px 0;
}

.footer-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    > div {
        width: 33%;
        &:last-child {
            display: flex;
            justify-content: flex-end;
        }
    }
}

.social-icons {
    display: flex;
    margin-left: 20px;
    a {
        margin-right: 6px;
        &:last-child {
            margin-right: 0;
        }
    }
}

.social-icons_circle {
    width: 35px;
    height: 35px;
    background-color: #070716;
    border-radius: 100%;
}

.facebook {
    background-image: url('../icons/footer/facebook.svg');
    background-repeat: no-repeat;
    background-position: center;
}

.twitter {
    background-image: url('../icons/footer/twitter.svg');
    background-repeat: no-repeat;
    background-position: center;
}

.linkedin {
    background-image: url('../icons/footer/linkedin.svg');
    background-repeat: no-repeat;
    background-position: center;
}

.instagram {
    background-image: url('../icons/footer/instagram.svg');
    background-repeat: no-repeat;
    background-position: center;
}

.footer-links-block {
    margin-top: 30px;

}
.blue-line {
    position: relative;
    &::before {
        content: '';
        display: block;
        width: 400px;
        height: 4px;
        background-color: #1FBAD6;
        position: absolute;
        top: -20px;
        right: -180px;
    }
}

.footer-links-top {
    display: flex;
    justify-content: space-between;
    a {
        font-weight: 300;
        font-size: 15px;
        color: #222;
        text-transform: uppercase;
        text-decoration: none;
    }
}

.footer-links-center {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
    flex-wrap: wrap;
    a {
        font-weight: 300;
        font-size: 11px;
        color: #999;
        text-transform: uppercase;
        text-decoration: none;
    }
}

.footer-links-down {
    display: flex;
    justify-content: center;
    margin-top: 5px;
    a {
        font-weight: 400;
        font-size: 11px;
        color: #1FBAD6;
        text-transform: uppercase;
        text-decoration: none;
    }
}

.download-icons {
    display: flex;
    a {
        margin-right: 5px;
        &:last-child {
            margin-right: 20px;
        }
    }
}

.app-store {
    background-image: url('../icons/footer/app-store.svg');
    width: 110px;
    height: 30px;
    background-repeat: no-repeat;
    background-position: center;
}

.google-play {
    background-image: url('../icons/footer/google-play.svg');
    width: 110px;
    height: 30px;
    background-repeat: no-repeat;
    background-position: center;
}

.windows-store {
    background-image: url('../icons/footer/windows-store.svg');
    width: 110px;
    height: 30px;
    background-repeat: no-repeat;
    background-position: center;
}