.work {
    padding: 20px 0 60px 0;
}

.rectangle-title {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 32px;
    width: 126px;
    text-transform: uppercase;
    background-color: black;
    color: white;
    text-align: center;
    margin: 0 auto;
    user-select: none;
}
.repet-title {
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    color: #222222;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    margin-top: 32px;
    margin-bottom: 20px;
}

.work-block {
    display: flex;
    align-items: center;
}

.work-circle {
    width: 116px;
    height: 116px;
    background: #1EACC7;
    border-radius: 100%;
}

.work-text {
    max-width: 370px;
    margin-left: 25px;
    margin-bottom: 25px;
    h4 {
        font-weight: 400;
        font-size: 17px;
        line-height: 26px;
        color: #1EACC7;
        margin: 0;
    }
    p {
        font-weight: 300;
        font-size: 15px;
        line-height: 21px;
        color: #202020;
        margin: 0;
    }
}
.work-image-1 {
    background-image: url('../icons/work/icon-1.png');
    background-position: center;
    background-repeat: no-repeat;
